.container {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	display: flex;
	justify-content: center;
	color: #212529;
}

.reset-password {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100%;
}

.reset-password .header {
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 8px;
	height: 40%;
}

.reset-password .header h1 {
	font-weight: 900;
	font-family: "Engagement", cursive;
	font-size: 68px;
}

.reset-password .header h2 {
	font-weight: 500;
	font-size: 20px;
}

.reset-password .header .error {
	color: #DC3545;
}

.reset-password .header .success {
	color: #198754;
}

.reset-password .header .instruction {
	font-size: 14px;
	color: #6C757D;
}

.reset-password .form {
	display: flex;
	flex-direction: column;
	gap: 8px;
	height: 40%;
}

.reset-password .form .input-group {
	display: flex;
	align-items: center;
	gap: 8px;
	padding: 8px 12px;
	background: #FFFFFF;
	border-radius: 8px;
	box-shadow: 0px 0px 1px #212529;
}

.reset-password .form .input-group input {
	border: 0;
	outline: 0;
	font-size: 16px;
	background: transparent;
	border-radius: 8px;
	width: 100%;
}

.reset-password .form .password-requirements {
	display: flex;
	flex-direction: column;
	gap: 2px;
	margin: 6px 2px;
}

.reset-password .form .password-requirements h4 {
	margin-bottom: 2px;
	font-weight: 500;
}

.reset-password .form .password-requirements p {
	font-size: 14px;
	display: flex;
	align-items: center;
	color: #198754;
}

.reset-password .form .password-requirements p.invalid {
	color: #DC3545;
}

.reset-password button {
	border: 0;
	outline: 0;
	font-size: 16px;
	padding: 8px;
	color: #212529;
	background: transparent;
	box-shadow: 0px 0px 1px #212529;
}

@media only screen and (max-width: 450px) {
	.reset-password .header {
		padding: 32px;
	}

	.reset-password .form {
		padding: 32px;
	}
}

@media only screen and (min-width: 450px) {
	.reset-password {
		padding: 32px;
		max-width: 400px;
	}

	.reset-password button {
		border-radius: 8px;
	}
}
